
.container-slider {
  max-width: 700px;
  height: 500px;
  margin: 100px auto 0;
  position: relative;
  overflow:visible;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}
@media screen and (max-width: 700px) {
  .container-slider {
    margin: 100px 10px 0;
  }
}
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.closeicon{
  position: absolute;
    top: -7%;
    left: 97%;
    width: 45px;
}
.closeicon:hover{
  cursor: pointer;
}
.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.prev {
  top: 50%;
  right: 95%;
  transform: translateY(-60%);
}
.next {
  top: 50%;
  left: 95%;
  transform: translateY(-60%);
}

.container-dots {
  position: absolute;
  top: 105%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}
.dot {
  width: 80px;
  /* height: 20px; */
  border-radius: 10%;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot.active {
  border:2px solid #ff6500;
  opacity: 50%;
}
