@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Kumbh Sans', sans-serif;
font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;
  position: relative;
  
}

.avatar{
  width: 22%;
}

.nav-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
}

.nav-cart__profile{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.banner{
  padding: 0;

}
.banner-product__image{
  position: relative;
}

.next{
  position: absolute;
  width: 9%;
    top: 50%;
    right: 5%;
    background-color: #ffffff;
    border-radius: 112%;
    padding-left: 9px;
    padding-bottom: 5px;

}
.nav-custom-items{
  position: absolute;
  z-index: 1;
  background: white;
  height: 100vh;
  right: 0%;
  width: 110%;
  top: -1.2rem;
}
.nav-custom-items-links{
  list-style: none;
    text-decoration: none;
    padding-top: 3.2rem;
    padding-left: 1rem;
}
.nav-link-custom{
  padding: 0.5rem;
}
.close{
  position: relative;
  z-index: 2;
}
.hidden{
  display: none;
}
a{
  text-decoration: none;
  color: black;
}
.previous{
  left: 5%;
  position: absolute;
  width: 9%;
    top: 50%;
    background-color: #ffffff;
    border-radius: 112%;
    padding-left: 7px;
    padding-bottom: 5px;
}

.decorative-text{
  text-transform: uppercase;
    font-size: 0.7rem;
    font-weight: 700;
    color: orange;
    letter-spacing: 0.6px;
}

.product-description{
  opacity: 50%;
  font-size: medium;
}

.inner_price--discount{
  display:  flex;
  align-items: center;
}

.price__discount{
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.inner_price--discount h2{
  font-size: x-large;
  font-weight: 700;
}
.nav-content{
  position: relative;
}
.alerterr{
  position: absolute;
  right: 2%;
  bottom: 70%;
  border-radius: 12px;
  background: #f7c8c8;
  padding: 1.3rem;
  font-size: 1.2rem;
}
.alerterrOut{
  position: absolute;
  right: 2%;
  bottom: 70%;
  border-radius: 12px;
  padding: 1.3rem;
  font-size: 1.2rem;
}
.alertsucc{
  position: absolute;
  right: 2%;
  bottom: 70%;
  border-radius: 12px;
  background: #c3f1c3;
  padding: 1.3rem;
  font-size: 1.2rem;

}

h5{
  background: #fee3b3;
    border-radius: 4px;
    padding: 5px;
    font-size: smaller;
    color: #ff6500;
    font-weight: bolder;
}

.price__discount p{
  opacity: 20%;
  font-weight: bold;
  text-decoration: line-through;
}

input{
  width: 100%;
    border: none;
    background: #f8f7f7;
    border-radius: 5px;
    padding: 1rem;
    text-align: center;
}

.input-Qty {
  position: relative;
}

.input-Qty > .minus {
  position: absolute;
  top: 38%;
  left: 15px;
  width: 5%;
}

.minus,.plus{
  cursor: pointer;
}

.input-Qty > .plus {
  position: absolute;
  top: 38%;
  right: 15px;
  width: 5%;
}
.active{
  border:2px solid #ff6500;
  opacity: 50%;
}
.animate__shakeX{
  border:4px dotted red
}
.th{
  border-radius: 12px;
}
.th:hover{
  cursor: pointer;
  opacity: 50%;
}
hr{
  width: 80%;
  margin: 1rem auto 1rem auto;
  opacity: .10;
  display: none;
}
.prod-thumbnail{
  display: none;
}

@media(min-width:600px){
  .prod-thumbnail{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 1rem;
  }
  .previous{
    display: none;
  }
  .next{
    display: none;
  }
  input{
    width: 65%;
    outline: none;
  }
  .input-Qty > .plus {
    position: absolute;
    top: 20%;
    right: 36%;
    width: 5%;
  }
  

}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
}
ul{
  margin: 0;
}

.modal_custom {
  position: fixed;
  left: 5%;
  width: 90%;
  padding: 1rem;
  border-radius: 14px;
  z-index: 30;
  -webkit-animation: slide-down 300ms ease-out forwards;
          animation: slide-down 300ms ease-out forwards;
}
/* position: fixed;
    left: 30%;
    width: 40%;
    /* padding: 1rem; */
    /* border-radius: 14px; 
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
    top: 15%;
} */
@media (min-width: 768px) {
  .modal {
    width: 40rem;
    left: calc(50% - 20rem);
  }
  .dot{
    width: 80px;
  }
  .nav-ham{
    display: none;
  }
  .nav-custom-items{
    display: block;
        position: unset; 
     z-index: unset; 
     background: unset; 
     height: unset; 
    right: unset; 
    width: unset; 
    top: unset;
  }
  
  .nav-custom-items-links{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  .nav-content{
    justify-content: center;
    align-items: baseline;
  }
  
  
}

@-webkit-keyframes slide-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media(min-width:768px){
  .prod{
    height: 100%
  }
  hr{
    display: block;
  }
  input{
    width: 100%;
  }
  .input-Qty > .plus {
    position: absolute;
    top: 38%;
    right: 15px;
    width: 5%;
  }
  .prod{
    border-radius: 12px;
    margin-left: 5px;
  }
}

@media (min-width:992px){
  .main-row{
    justify-content: center;
    align-items: center;
  }
  
  .prod{
    height: 65%;
    margin: auto;
    border-radius: 12px;
    width: 90%;
    display: flex;
  }
}
