span{
    position: absolute;
    right: 15%;
    bottom: 50%;
}

.ProductQuantityForm_minus__ctgIj,
.ProductQuantityForm_plus__329eL {
  cursor: pointer;
}

.ProductQuantityForm_cart-button__3RJHP {
  width: 100%;
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 12px;
  background: #ff8234;
